import React from 'react'
import { Appearance, classNames, Tappable, useAppearance } from '@vkontakte/vkui'

import './TableView.css'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { TableCell } from '../TableCell/TableCell'
import {
   selectActiveTableCurrentSequenceCell,
   selectActiveTableDisplayedCells,
   selectActiveTableOptionsSize,
   selectActiveTableStatus,
} from 'store/selectors/activeTable'
import { autoChangeColor, clickDisplayedCell, startSolution } from 'store/slices/activeTableSlice'
import { Icon28Play } from '@vkontakte/icons'
import { toRadians } from 'utils/number'
import { calcIdx, getX, getY } from 'utils/table'
// import { autoChangeColor } from 'store/slices/tableSlice'

interface TableViewProps {}

const MARGIN_CELL = 1

const COLORS = {
   [Appearance.LIGHT]: {
      primary: '#000',
      black: '#000',
      white: '#fff',
      red: '',
      stroke: '#000',
      secondary: '#ddd',
   },
   [Appearance.DARK]: {
      primary: '#fff',
      black: '#000',
      white: '#fff',
      red: '',
      stroke: '#ccc',
      secondary: '#ddd',
   },
}

export const getFillStyle = () => {}

export const TableView: React.FC<TableViewProps> = ({}) => {
   const dispatch = useAppDispatch()
   const appearance = useAppearance()

   const currentCell = useAppSelector(selectActiveTableCurrentSequenceCell)

   const containerRef = React.useRef<HTMLDivElement>(null)
   const canvasRef = React.useRef<HTMLCanvasElement>(null)
   const ctxRef = React.useRef<CanvasRenderingContext2D | null>(null)

   const clickedCellIdxRef = React.useRef<number>(-1)

   const tableStatus = useAppSelector(selectActiveTableStatus)

   const tableSize = useAppSelector(selectActiveTableOptionsSize)
   const displayedCells = useAppSelector(selectActiveTableDisplayedCells)

   const updateCanvasSizes = () => {
      if (!(canvasRef.current instanceof HTMLCanvasElement)) return

      const dpr = window.devicePixelRatio

      canvasRef.current.width = canvasRef.current.clientWidth * dpr
      canvasRef.current.height = canvasRef.current.clientHeight * dpr
   }

   const initCtx = () => {
      if (!(canvasRef.current instanceof HTMLCanvasElement)) return

      const _ctx = canvasRef.current.getContext('2d')
      if (_ctx) {
         ctxRef.current = _ctx
         ctxRef.current.imageSmoothingEnabled = true
      }
   }

   const draw = () => {
      if (!ctxRef.current) return

      ctxRef.current.clearRect(0, 0, ctxRef.current.canvas.width, ctxRef.current.canvas.height)
      const MARGIN_CELL = 3
      // const sizeCell = ctxRef.current.canvas.width / tableSize
      const sizeCell = (ctxRef.current.canvas.width - (tableSize + 1) * MARGIN_CELL) / tableSize

      const fontSize = sizeCell * 0.45
      const radisRoundRect = sizeCell * 0.08

      ctxRef.current.textAlign = 'center'
      ctxRef.current.textBaseline = 'middle'

      ctxRef.current.font = `500 ${fontSize}px/${fontSize}px -apple-system,system-ui,Helvetica Neue,Roboto,sans-serif`

      for (let idx = 0; idx < displayedCells.length; idx++) {
         const cell = displayedCells[idx]

         const col = idx % tableSize
         const row = Math.floor(idx / tableSize)

         const x = col * sizeCell + MARGIN_CELL * (col + 1)
         const y = row * sizeCell + MARGIN_CELL * (row + 1)

         ctxRef.current.save()

         ctxRef.current.lineWidth = 1

         //TODO || replace on &&
         if (cell?.background?.backgroundColorMode === 'custom' && cell.background?.backgroundColor) {
            ctxRef.current.fillStyle = ('#' + cell.background.backgroundColor) as string
         } else if (
            cell.background?.backgroundColorMode &&
            cell.background?.backgroundColorMode !== 'none' &&
            cell.background?.backgroundColorMode !== 'custom'
         ) {
            ctxRef.current.fillStyle = COLORS[appearance][cell.background?.backgroundColorMode]
         }

         ctxRef.current.translate(x, y)
         ctxRef.current.beginPath()

         if (cell.isRounded) {
            ctxRef.current.roundRect(0, 0, sizeCell, sizeCell, radisRoundRect)
         } else {
            ctxRef.current.rect(0, 0, sizeCell, sizeCell)
         }

         ctxRef.current.closePath()

         ctxRef.current.fill()

         if (cell.isOutline) {
            ctxRef.current.strokeStyle = '#a3adb8'
            // ctxRef.current.strokeStyle = COLORS[appearance].stroke
            ctxRef.current.stroke()
         }
         // ctxRef.current.fill()

         let textX = sizeCell / 2
         let textY = sizeCell - fontSize

         if (cell.transforms) {
            const angleTurn = +cell.transforms.turn
            if (angleTurn) {
               const tr = sizeCell / 2
               ctxRef.current.translate(tr, tr)
               ctxRef.current.rotate(toRadians(angleTurn))
               ctxRef.current.translate(-tr, -tr)
            }
            const flip = cell.transforms.flip as 'both' | 'x' | 'y'

            let scaleX = 1
            let scaleY = 1
            if (flip === 'both' || flip === 'x') {
               textX = (sizeCell / 2) * -1
               scaleX = -1
            }
            if (flip === 'both' || flip === 'y') {
               textY = (sizeCell / 2) * -1
               scaleY = -1
            }

            ctxRef.current.scale(scaleX, scaleY)
         }

         //TODO || replace on &&
         if (cell.symbol.colorMode === 'custom' && cell.symbol.color) {
            ctxRef.current.fillStyle = cell.symbol.color as string
         } else if (cell.symbol.colorMode && cell.symbol.colorMode !== 'custom') {
            ctxRef.current.fillStyle = COLORS[appearance][cell.symbol.colorMode]
         }

         ctxRef.current.fillText(cell.symbol.value, textX, textY)

         ctxRef.current.restore()
      }
   }

   const drawText = () => {}

   const drawCell = () => {}

   const onClick: React.MouseEventHandler<HTMLCanvasElement> = (e) => {
      const { offsetX, offsetY } = e.nativeEvent
      const idx = calcIdx(offsetX, offsetY, e.currentTarget.clientWidth, tableSize)

      dispatch(clickDisplayedCell(idx))
   }

   const onMouseMove: React.MouseEventHandler<HTMLCanvasElement> = (e) => {
      // console.log(e)

      const { offsetX, offsetY } = e.nativeEvent
      const idx = calcIdx(offsetX, offsetY, e.currentTarget.clientWidth, tableSize)
      // const x = getX(e.pageX, e.currentTarget)
      // const y = getY(e.pageY, e.currentTarget)
   }

   React.useEffect(() => {
      updateCanvasSizes()
      draw()
   }, [displayedCells])

   React.useEffect(() => {
      initCtx()
      updateCanvasSizes()
      draw()
   }, [])

   return (
      <div className={classNames('TableView')} ref={containerRef}>
         <div className='TableView__in'>
            <canvas className='TableView__canvas' ref={canvasRef} onClick={onClick} onMouseMove={onMouseMove} />
         </div>
      </div>
   )
}

export const TableView__old: React.FC<TableViewProps> = ({}) => {
   const tableStatus = useAppSelector(selectActiveTableStatus)

   const tableSize = useAppSelector(selectActiveTableOptionsSize)

   const dispatch = useAppDispatch()
   const currentCell = useAppSelector(selectActiveTableCurrentSequenceCell)
   const styles: React.CSSProperties = {
      gridTemplateColumns: `repeat(${tableSize}, 1fr)`,
      gridTemplateRows: `repeat(${tableSize}, 1fr)`,
   }

   return (
      <div className={classNames('TableView')}>
         <div className='TableView__in'>
            <div
               className={classNames(
                  'TableView__cells'
                  // tableStatus === null && 'TableView__cells--blur'
               )}
               style={styles}
            >
               <TableViewCells />
            </div>
         </div>
      </div>
   )
}
// {tableStatus === null && <TablePlayButton />}

const TableViewCells: React.FC = () => {
   const displayedCells = useAppSelector(selectActiveTableDisplayedCells)

   return (
      <>
         {displayedCells.map((cell) => (
            <TableCell key={cell.id} {...cell} />
         ))}
      </>
   )
}

const TablePlayButton: React.FC = () => {
   const dispatch = useAppDispatch()

   const onClickPlay = () => {
      dispatch(startSolution())
   }

   return (
      <div className='TablePlayButton'>
         <Tappable
            className='TablePlayButton__tappable'
            activeMode='background'
            activeEffectDelay={200}
            onClick={onClickPlay}
         >
            <Icon28Play className='TablePlayButton__icon' width={56} height={56} />
         </Tappable>
      </div>
   )
}
