import React from 'react'
import { useRouter } from '@happysanta/router'
import { Icon12ArrowUpRight } from '@vkontakte/icons'
import {
   Avatar,
   Card,
   CardGrid,
   FixedLayout,
   FormItem,
   Group,
   Panel,
   PanelHeader,
   PanelProps,
   RichCell,
   SegmentedControl,
   Separator,
   SimpleCell,
} from '@vkontakte/vkui'

import { Icon24DoneOutline, Icon24CancelOutline } from '@vkontakte/icons'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { TableCellSymbol } from 'components/ui/TableCellSymbol/TableCellSymbol'
import { TableCellBackground } from 'components/ui/TableCellBackground/TableCellBackground'
import { getRandomColor } from 'utils/color'

export const PanelProfile: React.FC<PanelProps> = (panelProps) => {
   // eslint-disable-next-line @typescript-eslint/no-unused-vars
   const router = useRouter()

   return (
      <Panel {...panelProps}>
         <PanelHeader separator={false}>Профиль</PanelHeader>
         <FixedLayout vertical='top' filled>
            <Separator wide />
         </FixedLayout>

         <Group mode='plain'>
            <CardGrid size='l'>
               <Card>
                  <RichCell before={<Avatar size={48} />} activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}>
                     user
                  </RichCell>
               </Card>
            </CardGrid>
         </Group>
         <Group>
            <FormItem top='Форма ячеек'>
               <SegmentedControl
                  options={[
                     {
                        label: (
                           <AvatarLabel>
                              <TableCellSymbol textSize='m' colorMode='primary' value='1' />
                           </AvatarLabel>
                        ),
                        value: '1',
                     },
                     {
                        label: (
                           <AvatarLabel2>
                              <TableCellSymbol textSize='m' colorMode='primary' value='2' />
                           </AvatarLabel2>
                        ),
                        value: '2',
                     },
                  ]}
               />
            </FormItem>
         </Group>

         <Group>
            <SimpleCell
               before={
                  <Avatar size={48} shadow={false} mode='image'>
                     <TableCellBackground background={{ backgroundColorMode: 'none' }}>
                        <TableCellSymbol value='1' colorMode='primary' />
                     </TableCellBackground>
                  </Avatar>
               }
               badgeAfterTitle={<Icon24CancelOutline width={12} height={12} fill='red' />}
               indicator={'1.02с'}
            >
               1.02с
            </SimpleCell>
            <SimpleCell
               before={
                  <Avatar size={48} shadow={false} mode='image'>
                     <TableCellBackground background={{ backgroundColorMode: 'red' }}>
                        <TableCellSymbol value='12' colorMode='white' />
                     </TableCellBackground>
                  </Avatar>
               }
               badgeAfterTitle={<Icon24DoneOutline width={12} height={12} fill='green' />}
               indicator={'3.02с'}
            >
               3.02с
            </SimpleCell>
            <SimpleCell
               before={
                  <Avatar size={48} shadow={false} mode='image'>
                     <TableCellBackground background={{ backgroundColorMode: 'red' }}>
                        <TableCellSymbol value='12' colorMode='white' />
                     </TableCellBackground>
                  </Avatar>
               }
               after={<Icon24DoneOutline width={28} height={28} fill='green' />}
            >
               4.21с
            </SimpleCell>
            <SimpleCell
               before={
                  <Avatar size={48} shadow={false} mode='image'>
                     <TableCellBackground background={{ backgroundColorMode: 'black' }}>
                        <TableCellSymbol value='12' colorMode='white' />
                     </TableCellBackground>
                  </Avatar>
               }
               after={<Icon24CancelOutline width={28} height={28} fill='red' />}
            >
               3.42с
            </SimpleCell>
            <SimpleCell
               before={
                  <Avatar size={48} shadow={false} mode='image'>
                     <TableCellBackground
                        background={{ backgroundColorMode: 'custom', backgroundColor: getRandomColor() }}
                     >
                        <TableCellSymbol value='24' colorMode='white' />
                     </TableCellBackground>
                  </Avatar>
               }
               after={<Icon24DoneOutline width={28} height={28} fill='green' />}
            >
               1.78с
            </SimpleCell>
         </Group>
      </Panel>
   )
}

const AvatarLabel: React.FC = ({ children }) => {
   return (
      <Avatar shadow={false} mode='image' size={28}>
         {children}
      </Avatar>
   )
}
const AvatarLabel2: React.FC = ({ children }) => {
   return (
      <Avatar shadow={false} mode='default' size={28}>
         {children}
      </Avatar>
   )
}
