import React from 'react'
import { Switch } from '@vkontakte/vkui'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectChangeableTableIsMarkSelected } from 'store/selectors/tableOptions'
import { setTableOptionsIsMarkSelected } from 'store/slices/tableOptions'

export const SwitchTableOptionsIsMarkSelected: React.FC = () => {
   const dispatch = useAppDispatch()
   const isMarkSelected = useAppSelector(selectChangeableTableIsMarkSelected)
   const onChangeIsMarkSelected: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      dispatch(setTableOptionsIsMarkSelected(e.target.checked))
   }
   return <Switch checked={isMarkSelected} onChange={onChangeIsMarkSelected} />
}
